"use client";

// API Service for Active Inference Agents following ADR-008 patterns

// Types matching our backend schemas
export interface BeliefState {
  beliefs: number[]; // q(s) - belief distribution
  entropy: number; // H[q(s)]
  confidence: number; // 1 - normalized entropy
  mostLikelyState: number; // argmax q(s)
  timestamp: number;
}

export interface GenerativeModel {
  A: number[][]; // Observation model matrix
  B: number[][][]; // Transition model tensor
  C: number[]; // Prior preferences
  D: number[]; // Initial beliefs
}

export interface PrecisionParameters {
  sensory: number; // γ - sensory precision
  policy: number; // β - policy precision
  state: number; // α - state precision
}

export interface ActiveInferenceConfig {
  template: "explorer" | "guardian" | "merchant" | "scholar";
  stateLabels: string[];
  numStates: number;
  numObservations: number;
  numActions: number;
  generativeModel: GenerativeModel;
  precisionParameters: PrecisionParameters;
  beliefState?: BeliefState;
  mathematicalConstraints?: {
    normalizedBeliefs: boolean;
    stochasticMatrices: boolean;
    precisionBounds: boolean;
  };
}

export interface Agent {
  id: string;
  name: string;
  status:
    | "idle"
    | "moving"
    | "interacting"
    | "planning"
    | "executing"
    | "learning"
    | "error"
    | "offline";
  personality?: {
    openness: number;
    conscientiousness: number;
    extraversion: number;
    agreeableness: number;
    neuroticism: number;
  };
  activeInference?: ActiveInferenceConfig;
  capabilities: string[];
  position: { x: number; y: number; z?: number };
  resources: {
    energy: number;
    health: number;
    memory_used: number;
    memory_capacity: number;
  };
  tags: string[];
  metadata: Record<string, any>;
  created_at: string;
  updated_at: string;
}

export interface CreateAgentRequest {
  name: string;
  personality?: {
    openness: number;
    conscientiousness: number;
    extraversion: number;
    agreeableness: number;
    neuroticism: number;
  };
  activeInference?: ActiveInferenceConfig;
  capabilities?: string[];
  initialPosition?: { x: number; y: number; z?: number };
  tags?: string[];
  metadata?: Record<string, any>;
}

// API Client Class
export class AgentsApi {
  private baseUrl: string;

  constructor(baseUrl: string = "/api/rest/agents") {
    this.baseUrl = baseUrl;
  }

  /**
   * Handle API response with proper error handling following ADR-008
   */
  private async handleResponse<T>(response: Response): Promise<T> {
    if (!response.ok) {
      let errorMessage = `HTTP ${response.status}: ${response.statusText}`;

      try {
        const errorData = await response.json();
        errorMessage = errorData.error || errorMessage;

        if (errorData.details) {
          console.error("API Error Details:", errorData.details);
        }
      } catch {
        // If JSON parsing fails, use the default error message
      }

      throw new Error(errorMessage);
    }

    try {
      return await response.json();
    } catch (error) {
      throw new Error("Failed to parse JSON response");
    }
  }

  /**
   * Create a new agent with Active Inference support
   */
  async createAgent(request: CreateAgentRequest): Promise<{ agent: Agent }> {
    try {
      const response = await fetch(this.baseUrl, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify(request),
      });

      return await this.handleResponse<{ agent: Agent }>(response);
    } catch (error) {
      throw new Error(
        `Failed to create agent: ${error instanceof Error ? error.message : "Unknown error"}`,
      );
    }
  }

  /**
   * Create an agent from a template with mathematical configuration
   */
  async createAgentFromTemplate(templateConfig: {
    template: ActiveInferenceConfig["template"];
    name: string;
    stateLabels: string[];
    precisionParameters: PrecisionParameters;
    tags?: string[];
    metadata?: Record<string, any>;
  }): Promise<{ agent: Agent }> {
    // Generate default Active Inference configuration for template
    const activeInferenceConfig: ActiveInferenceConfig = {
      template: templateConfig.template,
      stateLabels: templateConfig.stateLabels,
      numStates: templateConfig.stateLabels.length,
      numObservations: templateConfig.stateLabels.length, // Simplified: assume 1:1 mapping
      numActions: Math.min(templateConfig.stateLabels.length, 4), // Reasonable default
      generativeModel: {
        // Generate identity observation model as default
        A: Array(templateConfig.stateLabels.length)
          .fill(null)
          .map((_, i) =>
            Array(templateConfig.stateLabels.length)
              .fill(null)
              .map((_, j) => (i === j ? 1.0 : 0.0)),
          ),
        // Generate uniform transition model as default
        B: Array(Math.min(templateConfig.stateLabels.length, 4))
          .fill(null)
          .map(() =>
            Array(templateConfig.stateLabels.length)
              .fill(null)
              .map(() =>
                Array(templateConfig.stateLabels.length).fill(
                  1.0 / templateConfig.stateLabels.length,
                ),
              ),
          ),
        // Uniform preferences
        C: Array(templateConfig.stateLabels.length).fill(
          1.0 / templateConfig.stateLabels.length,
        ),
        // Uniform initial beliefs
        D: Array(templateConfig.stateLabels.length).fill(
          1.0 / templateConfig.stateLabels.length,
        ),
      },
      precisionParameters: templateConfig.precisionParameters,
      mathematicalConstraints: {
        normalizedBeliefs: true,
        stochasticMatrices: true,
        precisionBounds: true,
      },
    };

    const createRequest: CreateAgentRequest = {
      name: templateConfig.name,
      activeInference: activeInferenceConfig,
      capabilities: this.getDefaultCapabilities(templateConfig.template),
      tags: templateConfig.tags || [templateConfig.template],
      metadata: {
        ...(templateConfig.metadata || {}),
        templateType: templateConfig.template,
        mathematicallyValidated: true,
      },
    };

    return this.createAgent(createRequest);
  }

  /**
   * Get default capabilities for template type
   */
  private getDefaultCapabilities(
    template: ActiveInferenceConfig["template"],
  ): string[] {
    const capabilityMap = {
      explorer: ["movement", "perception", "planning", "learning"],
      guardian: ["perception", "communication", "planning", "memory"],
      merchant: [
        "communication",
        "social_interaction",
        "resource_management",
        "planning",
      ],
      scholar: ["learning", "memory", "planning", "communication"],
    };

    return (
      capabilityMap[template] || ["movement", "perception", "communication"]
    );
  }
}

// Export singleton instance
export const agentsApi = new AgentsApi();
